/**
* 案件->票据管理
* @module 案件
* @desc 票据管理下载上传
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center
                   width="80%">
            <div class="wrop" v-loading="loading">
                <table class="gridTable" v-loading="loading">
                    <tr>
                        <td class="label">部 门 ：</td>
                        <td colspan="4">
                            <span>{{info.collarOrgName}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td class="label">银行收据编号 ：</td>
                        <td>
                            <span>{{info.turnReceiptNum}}</span>
                        </td>
                        <td class="label">银行名称 ：</td>
                        <td>
                            <span>{{info.turnBank}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td class="label">缴款金额 ：</td>
                        <td>
                            <span>{{info.totalAmount}}元</span>
                        </td>
                        <td class="label">缴款银行日期 ：</td>
                        <td >
                            <span>{{info.turnDay|formatDate}}</span>
                        </td>
                    </tr>
                     <tr>
                        <td class="label">票据信息 ：</td>
                        <td colspan="4">
                            <span class="wrapper" v-html="info.billnumStr"></span>
                        </td>
                    </tr>

                </table>
                
            </div>

        </el-dialog>
    </div>
</template>
<script>
    import LjPagination from '../../../../../components/LjPagination'
    import {apiGetCaseQuotaInvoice,apibankReceiptListqueryByNum} from "../../../../../api/case";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    import md5 from 'js-md5'

    let that;
    export default {
        name: 'see',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: '查看'
            },
            id: {
                type: String,
                default: ''
            },

        },
        components:{
           LjPagination
        },
        data() {
            return {
                loading: false,
                list: [],
                info:{},
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
            }
        },
        methods: {
            //关闭弹窗
            closeDialog() {
                this.$emit('update:show', false)
            },

            // 提交
            submitForm() {
                this.closeDialog();
            },
            //初始化
            initPage(){
                if(this.id){
                    this.form.quotaId = this.id;
                    let str="";
                    apibankReceiptListqueryByNum({turnReceiptNum:this.id}).then(res => {
                        this.loading = false;
                        this.info=res.data;
                        res.data.caseQuotaDetailList.forEach(item=>{
                             str+= item.turnBank+' : '+item.turnReceiptNum +"（"+item.quotaInvoiceAmount+"元"+"）;" +  "\n";
                        });
                       this.info.billnumStr = str;
                    });
                }
                
            }

        },
        filters:{
            formatDate(value){
                return formatDate(value,"yyyy-MM-dd")
            },
            quotaStatus(value){
                return quotaStatus[value]
            }
        },
        watch:{
           show(value){
               if(value){
                   this.initPage()
               }
           }
        }
    }
</script>
<style lang='less' scoped>
   .gridTable{
       margin-bottom: 5px
   }
   .wrapper{
       white-space: pre-wrap;
   }
</style>


