/**
* 案件->票据管理
* @module 案件
* @desc 票据管理表格
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="search-wrop">
            <lj-search-from  @searchList="searchList" />
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}" v-loading='loading'
                  @row-click="seeFunction"
                  border size="mini">
            <el-table-column align="center" label="单位" prop="collarOrgName" show-overflow-tooltip/>
            <el-table-column align="center" label="缴款银行" prop="turnBank" show-overflow-tooltip width="100"/>
            <el-table-column align="center" label="缴款金额" prop="totalAmount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="收据编号" prop="turnReceiptNum" show-overflow-tooltip />
            <el-table-column align="center" label="缴款日期" prop="turnDay" show-overflow-tooltip width="150">
                <template slot-scope="scope">
                    {{scope.row.turnDay | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="缴款状态" prop="quotaStatus" width="100">
                <template slot-scope="scope">
                    {{scope.row.quotaStatus | quotaStatus}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                icon="el-icon-search"
                                type="primary"
                                v-if="buttonCheck('see')"
                        ></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link @click.stop="delateFunction(scope.$index, scope.row)" class="el-icon-delete btn-css"
                                 type="danger" v-if="buttonCheck('delete')"><i></i></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                       class="page-css"
        />

        <!-- 查看 -->
        <seecontent   :show.sync="seeObj.show"
                    :title="seeObj.title" :id="seeObj.editId" />
    </div>
</template>

<script>
    import LjPagination from '../../../../../components/LjPagination'
    import LjSearchFrom from '../../../../../components/LjSearchForm'
    import seecontent from "./seecontent";
    import {
        apibankReceiptListqueryBankDetailSearch
    } from "../../../../../api/case";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    export default {
        name: "banklist",
        data() {
            return {
                caseProcessList: [],
                docStyleArr: [],
                loading: true,
                
                //弹出框数据
               
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseTypeList: [],
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    keyWord:""
                },
                total: 0,
                list: [],
                idstr: '',
            }
        },
        components: {
             LjPagination, LjSearchFrom,seecontent
        },


        methods: {

            //查询+搜索
            getList() {
                apibankReceiptListqueryBankDetailSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });

            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.turnReceiptNum;
                this.seeObj.title = '查看银行收据清单';
                this.seeObj.show = true;
            },
            
            //搜索点击
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                apibankReceiptListqueryBankDetailSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },

           
        },
        

        filters: {
            quotaStatus(val) {
                return val==1?'未使用':(val==2?'已使用':(val==3?'已上缴，待确认':(val==4?'已确认':'')))
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            },

        },
        created() {
            this.getList();

        },
    }
</script>

<style scoped>
    .search-wrop {
        margin-bottom: 10px
    }

    .btn-css {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
